//专门创建整个应用的路由器
import VueRouter from 'vue-router'
import Vue from 'vue'
Vue.use(VueRouter)
import About from '../pages/About'
import Home from '../pages/Home'
import News from '../pages/News'
import Message from '../pages/Message'
import Detail from '../pages/Detail'

//创建一个路由器
const router = new VueRouter({
    mode:'history',
    routes:[
        {
            name:'about',
            path:'/about',
            component: About,
            meta:{title:'about'}
        },
        {
            name:'home',
            path:'/home',
            meta:{title:'home'},
            component: Home,
            children:[
                {
                    //子路由不用写斜杆
                    meta:{isAuth:true,title:'news'},
                    name:'news',
                    path:'News',
                    component:News
                },
                {
                    meta:{isAuth:true,title:'message'},
                    name:'message',
                    path:'Message',
                    component:Message,
                    children:[
                        {
                            name:'detail',
                            path:'Detail/:id/:title',
                            meta:{title:'detail'},
                            component:Detail,
                            // 对象模式,所有参数以props的形式传给Detail组件
                            // props:{id:1, title:'hello'}
                            // 布尔模式,所有参数以props的形式传给Detail组件
                            // props:true
                            // 函数模式
                            props($route){
                                return {id: $route.params.id, title: $route.params.title}
                            }
                        }
                    ]
                }
            ]
        },
    ]
})
// 当一个导航触发时，全局前置守卫按照创建顺序调用。守卫是异步解析执行，此时导航在所有守卫 resolve 完之前一直处于 等待中。
// 每个守卫方法接收三个参数：
// to: Route: 即将要进入的目标 路由对象
// from: Route: 当前导航正要离开的路由
// next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
// next(): 进行管道中的下一个钩子。如果全部钩子执行完了，则导航的状态就是 confirmed (确认的)。
//      next(false): 中断当前的导航。如果浏览器的 URL 改变了 (可能是用户手动或者浏览器后退按钮)，
// 那么 URL 地址会重置到 from 路由对应的地址。
//      next('/') 或者 next({ path: '/' }): 跳转到一个不同的地址。当前的导航被中断，然后进行一个新的导航。
// 你可以向 next 传递任意位置对象，且允许设置诸如 replace: true、name: 'home' 之类的选项以及任何用在
// router-link 的 to prop 或 router.push 中的选项。
//      next(error): (2.4.0+) 如果传入 next 的参数是一个 Error 实例，则导航会被终止且该错误会被传递给
// router.onError() 注册过的回调。

//初始化的时候被调用,路由切换之前调用
router.beforeEach((to, from, next)=>{
    // if (to.meta.isAuth){
    //     if (to.path==='/home/News'){
    //         next()
    //     }else{
    //         alert('无权限')
    //     }
    // }else{
    //     next()
    // }
    next()
})
//初始化的时候被调用,路由切换之后调用
router.afterEach((to, from)=>{
    console.log(to, from)
    document.title = to.meta.title
})
export default router
