<template>
  <div>
    <h1>home</h1>
<!--    浏览器历史记录添加方式，push(默认)追加，replace覆盖上一条记录-->
    <router-link replace to="/home/News" active-class="active">News</router-link>
    <br>
    <router-link replace to="/home/Message" active-class="active">Message</router-link>
<!--    切换组件时保持之前的组件不被销毁，一直挂载-->
    <keep-alive include="News">
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "Home"
}
</script>

