<template>
  <div>
    <ul>
      <li v-for="m in messageList" :key="m.id">
        <!--        跳转路由并携带params参数，to的字符串写法-->
        <!--                <router-link :to="`/home/Message/Detail/${m.id}/${m.title}`">-->
        <!--                  {{m.title}}-->
        <!--                </router-link>-->
        <!--        跳转路由并携带params参数，to的对象写法-->
<!--        <router-link :to="{-->
<!--          //必须为name,不能为path-->
<!--          name:'detail',-->
<!--          params:{-->
<!--            id: m.id,-->
<!--            title: m.title-->
<!--          }-->
<!--        }">-->
<!--          {{ m.title }}-->
<!--        </router-link>-->
        <button @click="pushShow(m)">push</button>
        <button @click="replaceShow(m)">replace</button>
      </li>
    </ul>
    <br>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      messageList: [
        {id: '01', title: 'Message01'},
        {id: '02', title: 'Message02'},
        {id: '03', title: 'Message03'}
      ]
    }
  },
  methods:{
    pushShow(m){
      const router = this.$router
      // 字符串
      // router.push('/home/Message/Detail')
      // 对象
      // router.push({ path: '/home/Message/Detail' })
      // 命名的路由
      router.push({ name: 'detail', params: { id: m.id, title: m.title}})
      // 带查询参数，变成 //home/Message/Detail?id=123, title=123
      // router.push({ path: '/home/Message/Detail', query: { id: '123', title: '123' }})
    },
    replaceShow(m){
      const router = this.$router
      router.replace({ name: 'detail', params: { id: m.id, title: m.title}})
    }
  }
}
</script>

